.main {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
/*-----------------------------------*/
.Box {
  padding: 1%;
  width: 100vw;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}
/*-----------------------------------*/
.rightBox {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
